import { isNullOrUndefined, registerArtist } from "../constants/globals"

export class Artist {
    name
    slug
    domain
    about
    bio
    metadata
    instagramUsername

    constructor(name, slug, domain, about, bio, metadata, instagramUsername) {
        this.name = name
        this.about = about
        this.slug = slug
        this.domain = domain
        this.bio = bio
        this.metadata = metadata
        this.instagramUsername = instagramUsername
    }

    static registerFromContentful(contentfulArtist) {
        const artist = this.fromContentful(contentfulArtist)
        registerArtist(artist)
        return artist
    }

    static fromContentful(contentfulArtist) {
        if (isNullOrUndefined(contentfulArtist)) {
            return null
        }
        console.log(contentfulArtist)
        return new Artist(contentfulArtist.name, contentfulArtist.slug,
            contentfulArtist.domain, contentfulArtist.about.about, contentfulArtist.bio,
            contentfulArtist.metadata, contentfulArtist.instagram)
    }
}
